body{
    background-color: #ebebeb;
    margin: 0 auto;
    /* background-image: linear-gradient(to right, #4CA1AF, #C4E0E5); */
    /* background-image: url(../imgs/4k-wallpaper-air-atmosphere-2310713.jpg); */
}

.formContainer{
    width: 100%;
    height: 20vh;
    padding: 15px 0px 15px 0px;
    /* border: 2px solid blue; */
    background-color:rgb(41, 93, 145);
}


form{
    /* border: 2px solid red; */
    display: grid;
    justify-content: center;
    float: left;
    width: 88vw;
}

input {
    width: 300px;
    height: 2vh;
    border-radius: 5px;
    padding: 8px;
    border-style: none;
    margin-top: 10px;
}

button {
    width: 315px;
    height: 4vh;
    border-radius: 5px;
    background-color: #4CA1AF;
    margin-top: 10px;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 15px;
    font-weight: 500;
}

button:hover{
    background-image: linear-gradient(to right, rgb(121, 189, 199), rgb(127, 175, 184));
    transition: .4s;
}

button:focus, input:focus {
    outline: none;
  }


@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 568px)  {

    input {
        width: 260px;
    }
    button {
        width: 277px;
    }
}


@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px)  {

    .formContainer {
        width: 100%;
        height: 15vh;
    }
    form {
        float: none;
        width: 94vw;
    }
    input {
        width: 50vw;
    }
    button {
        width: 52vw;
        height: 35px;
    }
}


@media only screen 
and (min-device-width : 1024px) 
and (max-device-width : 1366px)  { 
    .formContainer {
        width: 100%;
        height: 5vh;
        /* padding: 20px; */
    }
    form {
        display: flex;
        float: none!important;
        width: 100%;
    }
    input {
        margin-top: 0px;
        margin-right: 10px;
        height: 20px;
    }
    button {
        margin-top: 0px;
        height: 35px;
    }
    
}

@media (min-width: 1366px) {
    .formContainer {
        width: 100%;
    }
    form {
        display: flex;
        float: none!important;
    }
    input {
        margin-top: 0px;
        margin-right: 10px;
    }
    button {
        margin-top: 0px;
        height: 3.3vh;
    }

}