.weatherContainer {
    width: 100vw;
    display: grid;
    /* border: 2px solid gold; */
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 40px;    
}

.weatherInner {
    width: 70vw;
    /* border: 2px solid palevioletred; */
    background-color: white;
    padding: 40px;
    box-shadow: 10px 9px 14px -11px rgba(0,0,0,0.75);
    border-radius: 5px;
    background-image: url(../imgs/4k-wallpaper-air-atmosphere-2310713.jpg);
    background-size: cover;
    background-repeat: none;
}

.weatherInner h1 {
    color: white;
}

.innerGrid{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.weatherIcon{
    width: 100px;
}

.weatherCity{
    font-size: 20px;
}

.leftBox {
    /* border: 2px solid deeppink; */
    background-color: #4CA1AF;
    padding: 20px;
    display: grid;
    justify-content: center;
    color: white;
    opacity: 0.9;
}

.rightBox {
    /* border: 2px solid greenyellow; */
    background-color: rgb(41, 93, 145);
    padding: 20px;
    display: grid;
    justify-content: center;
    color: white;
    font-weight: 500;
    opacity: 0.9;
}