.forecastContainer{
    width: 100%;
    /* border: 2px solid deeppink; */
    display: grid;
    /* background-color: rgb(41, 93, 145); */
    background-image: linear-gradient(to right, #4CA1AF, #C4E0E5);
}

.forecastContainer h1 {
    color: white;
    margin-top: 70px;
    padding-left: 20px;
}

.forecastInner {
    width: 95%;
    /* border: 2px solid gold; */
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    grid-gap: 10px;
}

.forecastBox {
    width: 80%;
    padding: 15px;
    background-color: white;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 10px 9px 14px -11px rgba(0,0,0,0.75);
}
.forecastBox h2{
    color: rgb(41, 93, 145);
}